<template>
	<div class="header" :style="ColorSt">
		
		<slot></slot>
	</div>
</template>

<script>
	export default{
		props:{
			activeColor:{
				type:String,
				default:'#F38B96'
			}
		},
	  computed:{
		 ColorSt(){
			 return this.activeColor ? {backgroundColor:this.activeColor}:{}
		 }
	  },
	
	}
</script>

<style>
	/*背景颜色不能写死，让父组件传颜色过来 */
	
	.header {
		/* width: 750px; */
		width: 100vw;
		height:45px;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 9999;
	}
	
	
	
</style>
